import React, { Fragment, useState, useEffect, useContext } from 'react';
import axios from 'axios';
import qs from 'qs';
import StoreStatus from './StoreStatus';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';
import Avatar from '@material-ui/core/Avatar';
import { useParams, Link, useHistory } from 'react-router-dom';
import Divider from '@material-ui/core/Divider';
import { UserContext } from './App';
import { USER_ID, AUTH_CODE, AUTH_TOKEN } from '../config';
import firebase from './firebase';
import { connectableObservableDescriptor } from 'rxjs/internal/observable/ConnectableObservable';

const db = firebase.firestore();
const useStyles = makeStyles(theme => ({
    medium: {
        width: theme.spacing(20),
        height: theme.spacing(20),
    },
    small: {
        width: theme.spacing(4),
        height: theme.spacing(4),
        paddingLeft: '10px'
    },
    divider: {
        height: '6px',
        backgroundColor: '#f2f2f2'
    }
}));


export default ({ items }) => {
    let history = useHistory();
    const classes = useStyles();
    let { goods_code } = useParams();
    const [item, setItem] = useState({});
    const [buttonClicked, setButtonClicked] = useState(false);

    const user = useContext(UserContext);

    useEffect(() => {
        items
            .filter(item => item.goodsCode === goods_code)
            .forEach(item => {
                const tmpItem = {
                    goodsName: item.goodsName,
                    brandName: item.brandName,
                    realPrice: item.realPrice,
                    goodsImgS: item.goodsImgS,
                    brandIconImg: item.brandIconImg
                };
                setItem(tmpItem);
            });

        window.scrollTo(0, 0);
    }, []);

    // TR_ID: service_20201220_12345678
    const genTrid = () => {
        const today = new Date();
        const fullYear = today.getFullYear();
        const month = today.getMonth() + 1;
        const date = today.getDate();
        return `service_${fullYear}${month}${date}_${Math.round(Math.random() * 100000000)}`;
    }

    // 
    const handlePurchase = async () => {
        if(buttonClicked) return;
        const trId = genTrid();
        setButtonClicked(true);
        // 
        let response = await axios({
            url: '/bizApi/send/',
            method: 'post',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data: qs.stringify({
                api_code: '0204',
                custom_auth_token: AUTH_TOKEN,
                custom_auth_code: AUTH_CODE,
                dev_yn: 'N',
                goods_code: `${goods_code}`,
                mms_msg: `mms`,
                mms_title: `mms`,
                callback_no: `01072122774`,
                phone_no: `01072122774`,
                tr_id: trId,
                user_id: USER_ID,
                gubun: 'I',
            })
        });
        let data = response.data.result;
        /*
            code    4   ( )
            message 50  ( )
            pinNo   20  (gubunY  )
            orderNo 20 

            {
                code: 0000,
                message: null, 
                result: {
                    code: 0000, 
                    message: null, 
                    result: {
                        orderNo: 20190812000000,
                        pinNo: 900343630367,
                        "couponImgUrl": "http://t.giftishow.co.kr/mms_90012345678_01.jpg"
                    } 
                }
            }
        */
        console.log(data);
        //console.log(genTrid());
        //    DB   
        if (data.code === '0000') {

            localStorage.setItem('couponAdded', 'true');
            const resultData = data.result;
            console.log('tr_id: ', trId);
            db.collection(`members/${user.uid}/coupons`).doc(trId)
                .set({
                    ...resultData, 
                    trId: trId, 
                    createdAt: firebase.firestore.FieldValue.serverTimestamp(), 
                })
                .then(function () {
                    console.log('DB ...');
                    // cash update
                    db.collection('members').doc(user.uid)
                        .update({cash: user.cash - item.realPrice});
                    // home 
                    history.push(`/${user.uid}`);
                })
                .catch(function () {
                    console.log('DB ...');
                })
        } else {
            //            
            alert('   !');
            setButtonClicked(false);
        }


    }

    //  
    const handleCancel = () => {
        history.push(`/${user.uid}`);
    }
    return (
        <Fragment>
            <Box>
                <StoreStatus displayImage={user.displayImage} displayName={user.displayName} cash={user.cash} />
                <Box>
                    <Box height={""} display="flex" alignItems="center">
                        <Box px={4}>
                            <Avatar src={item.goodsImgS} className={classes.medium} variant="square" />
                        </Box>
                        <Box pr={4}>
                            <Box>{item.goodsName}</Box>
                            <Box display="flex" alignItems="center">
                                <Box color="gray" fontSize={14}>{item.brandName}</Box>
                                <Avatar src={item.brandIconImg} className={classes.small} />
                            </Box>
                            <Box color="#72B4B4">{item.realPrice}</Box>
                        </Box>
                    </Box>
                    <Divider className={classes.divider} />
                    <Box py={3}>
                        <Box display="flex" px={4} py={0} justifyContent="space-between">
                            <Box>  </Box>
                            <Box>{user.cash}</Box>
                        </Box>
                        <Box display="flex" px={4} py={0} justifyContent="space-between">
                            <Box> </Box>
                            <Box>{item.realPrice}</Box>
                        </Box>
                    </Box>
                    <Divider className={classes.divider} />
                    <Box display="flex" py={3} px={4} justifyContent="space-between">
                        <Box> </Box>
                        <Box color="#72B4B4">{user.cash - parseInt(item.realPrice)}</Box>
                    </Box>
                    <Divider className={classes.divider} />
                    <Box py={5}>
                        <Box textAlign="center" lineHeight="25px">
                               , <br />
                            <span style={{ color: '#72B4B4' }}><Link to="/api/couponbox"> </Link></span> <br />
                           <br />
                         .<br />
                        </Box>
                        <Box textAlign="center" pt={5} pb={2} style={{ textDecoration: 'underline' }} onClick={handleCancel} disabled={true}><a></a></Box>
                    </Box>
                    <Box textAlign="center" bgcolor="#72B4B4" py={2} color="white" onClick={handlePurchase} disabled={buttonClicked}></Box>
                </Box>
            </Box>
        </Fragment>
    )
}
